<!-- 线进度条 -->
<template>
  <div class="progressLine clearfix">
    <span>{{progessConfig.title}}{{progessConfig.percentage}}%</span>
    <div class="progress-container">
      <div
        class="progress-item"
        :style="{backgroundImage:progessConfig.color,width:progessConfig.percentage+'%'}"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      colorList: [
        { type: "shock", color: "rgba(106, 111, 152, 1)" },
        {
          type: "seeEmpty",
          color: "-webkit-linear-gradient(0deg, #1addb9, #07bb6f)"
        },
        {
          type: "seeMore",
          color: "-webkit-linear-gradient(0deg, #fb78a0, #f64367)"
        }
      ]
    };
  },
  props: {
    progessConfig: Object
  },
  mounted() {}
};
</script>
<style  scoped>
.progressLine span {
  font-size: 40px;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  line-height: 56px;
  margin-right: 80px;
  float: left;
}
.progress-container {
  position: relative;
  float: left;
  width: 486px;
  height: 31px;
  top:15px;
  border-radius: 50px;
  background: rgba(36, 39, 70, 1);
}
.progress-item {
  height: 32px;
  position: absolute;
  right: 0;
  top: 0;
  height: 31px;
  border-radius: 50px;
}
</style>